import axios from "axios";


async function SendFormData(MAIL_DATA={}) {
    const MAIL_URL = "https://script.google.com/macros/s/AKfycbxSVrkuzB7nVewqGyRiqeY9u47CwOGVOTMxfjJYptLSRKicy3k9/exec";
    const options = {
        method: 'POST',
        headers: { 'content-type': 'application/x-www-form-urlencoded' },
        data: JSON.stringify(MAIL_DATA),
        url: MAIL_URL,
    };
    axios(options);
     
}

export default SendFormData;


