import React, { useState } from 'react'
import SendFormData from '../Utils/SendFormData';

export default function Contact() {

    const [showFormMessage, setShowFormMessage] = useState(false);

    function handleSubmit(e) {
        e.preventDefault();

        var MAIL_DATA = {};
        MAIL_DATA.col1 = e.target.contactName.value;
        MAIL_DATA.col2 = e.target.email.value;
        MAIL_DATA.col3 = e.target.contactMessage.value;
        MAIL_DATA.type = 1;

        SendFormData(MAIL_DATA);

        setShowFormMessage(true);
        e.target.reset();

    }


    return (
        <div className="margin-top-responsive contact-grid-container">

            <div className="w3l-contact-hny-6">
                <form action="#" method="post" className="contact-form" onSubmit={handleSubmit}>
                    <h5>Contacts</h5>
                    <p>We want to hear from you</p>
                    {showFormMessage && <div>
                        <h5 style={{ color: "green", fontStyle: "bold", fontSize: "1.2em" }}>
                            Your request is submitted. We will contact you shortly.
                            </h5>
                    </div>}
                    <input type="text" placeholder="Name" name="contactName" id="w3lName" required />
                    <input type="email" name="email" placeholder="Email" id="w3lSender" required />
                    <textarea placeholder="Message" name="contactMessage" id="w3lMessage" required></textarea>
                    <button type="submit" className="btn buttonbg">Send Now</button>
                </form>
            </div>

            <div className="" style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>

                <div className="grid">
                    <div className="adress-lavi-info">
                        <h5>Visit Us</h5>
                        <h3>

                            <p> Golden Dreams Adventure,
                            <br />
                            13 Confesor Street, Leganes, Iloilo
                            <br />5003 Philippines
                        </p>
                        </h3>
                    </div>
                    <div className="adress-lavi-info" style={{ marginTop: "10px" }}>
                        <h5>Contact Us</h5>
                        <h3>
                            <p><a href="tel:+66982872874"> + 6698 287 2874</a></p>
                            <p><a href="mailto:rsvn@goldendreamsadventure.com" className="mail">rsvn@goldendreamsadventure.com</a></p>

                        </h3>
                    </div>
                </div>

            </div>
        </div>
    )
}
