import React, { useState } from 'react';
import SendFormData from "../Utils/SendFormData";

export default function ContactForm() {

    const [showEmailMessage, setShowEmailMessage] = useState(false);

    async function handleContactSubmit(e) {
        e.preventDefault();
        const MAIL_DATA = {};
        MAIL_DATA.col1 = e.target.name.value;
        MAIL_DATA.col2 = e.target.email.value;
        MAIL_DATA.col3 = e.target.number.value;
        MAIL_DATA.col4 = e.target.date.value;

        SendFormData(MAIL_DATA);
        setShowEmailMessage(true);
        e.target.reset();

    }

    return (
        <section className="w3l-form-my-mian">
            <div className="form-botm-cont">
                <div className="">
                    <div className="form-inner-cont">
                        <div className="d-grid top-content-7">
                            <h4 className="left-text-inti">Get the Best Holiday Planned by Experts!</h4>
                            <a href="tel:+66982872874"><b>Tel:</b> +6698 287 2874</a>
                        </div>
                        {showEmailMessage && <div >
                            <h5 style={{ color: "green", fontStyle: "bold", fontSize: "1.2em" }}>
                                Your request is submitted. We will contact you shortly.
                            </h5>
                        </div>}
                        <form action="#" method="post" className="d-grid signin-form" onSubmit={handleContactSubmit}>
                            <div className="form-input">
                                <input type="text" name="name" placeholder="Your Name" required />
                            </div>
                            <div className="form-input">
                                <input type="email" name="email" placeholder="Email" required />
                            </div>
                            <div className="form-input">
                                <input type="number" name="number" placeholder="Mobile Number" required />
                            </div>
                            <div className="section-input">
                                <div className="sec-grid-1">
                                    {/* <select name="carlist" form="carform">
                                        <option value="audi">Oct 2020</option>
                                        <option value="audi">Nov 2020</option>
                                        <option value="audi">Dec 2020</option>
                                        <option value="audi">Jan 2021</option>
                                        <option value="audi">Dec 2020</option>
                                        <option value="audi">Dec 2020</option>
                                        <option value="audi">Dec 2020</option>
                                        <option value="audi">Dec 2020</option>
                                    </select> */}
                                    <input type="date" name="date" required placeholder="tour date" />
                                </div>
                            </div>
                            <button className="btn buttonbg">Submit</button>
                        </form>
                        <p className="sub-paragraph">By submitting this form, you authorize Travel and its partners to contact you for this enquiry.</p>
                    </div>
                </div>
            </div>
        </section>
    )
}
